<template>
  <main-component
      :beeps="beeps.data()"
      :loading="beeps.loading"
      :loaded="beeps.loaded"
      @reload="beeps.reload()"
  ></main-component>
</template>

<script>
// @ is an alias to /src
import Connector from "@/stuff/Connector";
import MainComponent from '@/components/MainComponent.vue'
import BeepsContainer from "@/stuff/BeepsContainer";

export default {
  components: {
    MainComponent
  },
  data() {
    return {
      beeps: new BeepsContainer(new Connector),
    }
  },
  mounted() {
    this.beeps.reload()
  }
}
</script>
